html {
  cursor: none;
}

.cursor {
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background: black;
  position: absolute;
  border-radius: 50%;
}

.cursor:before {
  animation: cycle-colors 2s linear infinite;
  border: #fff solid 2px;
  border-radius: 9999px;
  box-sizing: border-box;
  content: " ";
  height: 140%;
  left: -20%;
  opacity: 0.6;
  position: absolute;
  top: -20%;
  transform: scale(0.714);
  width: 140%;
  z-index: 1;
  animation-delay: 0.2s;
}

.cursor__follower {
  transition: transform 1s;
  width: 10px;
  height: 10px;
  background-color: red;
  animation: cycle-colors 0.2 linear infinite;
}

body {
  background: linear-gradient(-45deg, #000, #000, #9e4f37, #b12e60, #1a7799, #168a6f, #000);
  background-size: 1000%;
  width: 100%;
  height: 100vh;
  font-family: "Exo", sans-serif;
  color: #fff;
  animation: gradient 30s ease infinite;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 25px;
}

h4 {
  font-size: 20px;
}

.preheader {
  background-color: #141414;
  height: 10px;
}

.container {
  max-width: 1200px;
  margin: auto;
}

header {
  width: 100%;
  text-align: center;
}

.outer {
  display: flex;
  flex-wrap: wrap;
  max-width: 800px;
  margin: auto;
  justify-content: space-around;
}

@media only screen and (min-width: 550px) {
  .outer {
    flex-wrap: nowrap;
  }
}

.outer .outer_avatar {
  margin: auto;
  width: 125px;
  height: 125px;
  position: relative;
}

.outer .outer_avatar img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
  z-index: 2;
}

.outer .outer_avatar:after, .outer .outer_avatar:before {
  animation: cycle-colors 2s linear infinite;
  border: #fff solid 2px;
  border-radius: 9999px;
  box-sizing: border-box;
  content: " ";
  height: 140%;
  left: -20%;
  opacity: 0.6;
  position: absolute;
  top: -20%;
  transform: scale(0.714);
  width: 140%;
  z-index: 1;
  animation-delay: 1s;
}

.outer .outer_avatar:after {
  animation: pulse 1s linear infinite;
}

.outer .outer_info {
  margin-left: 20px;
}

@keyframes pulse {
  to {
    opacity: 0;
    transform: scale(1);
  }
}

@keyframes cycle-colors {
  0% {
    border-color: red;
  }
  25% {
    border-color: #80ff00;
  }
  50% {
    border-color: cyan;
  }
  75% {
    border-color: #8000ff;
  }
  100% {
    border-color: red;
  }
}

footer {
  text-align: center;
}

footer .social-icons {
  height: 100px;
}

footer .social-icons a {
  transition: all 0.2s ease-in-out;
  opacity: 0.3;
}

footer .social-icons a img {
  width: 50px;
}

footer .social-icons a:hover {
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
  opacity: 1;
}
